<template>
  <b-tab>
    <template slot="title">
      <div :class="slot_name_assoc[slot_name]"></div>
    </template>
    <slot></slot>
  </b-tab>
</template>

<script>
var slot_name_assoc = {
  'Body': 'body',
  'Decal': 'decal',
  'Paint Finish': 'paint',
  'Wheels': 'wheels',
  'Rocket Boost': 'boost',
  'Topper': 'topper'
}

export default {
  data() {
    return {
      slot_name_assoc
    }
  },
  props: {
    slot_name: String
  }
}
</script>

<style scoped>
.body {
  width: 32px; height: 32px;
  background: url('../assets/img/sprites/tabs.png') -10px -10px;
}

.decal {
  width: 32px; height: 32px;
  background: url('../assets/img/sprites/tabs.png') -114px -10px;
}

.paint {
  width: 32px; height: 32px;
  background: url('../assets/img/sprites/tabs.png') -62px -62px;
}

.wheels {
  width: 32px; height: 32px;
  background: url('../assets/img/sprites/tabs.png') -114px -62px;
}

.boost {
  width: 32px; height: 32px;
  background: url('../assets/img/sprites/tabs.png') -62px -10px;
}

.topper {
  width: 32px; height: 32px;
  background: url('../assets/img/sprites/tabs.png') -10px -62px;
}
</style>